import React from 'react'
import GifsItem from './GifsItem'


const GifsGrid = ({ items }) => {
    return  (
        <section className='cards' >
            {items.map((item) => (
                <GifsItem key={item.id} item={item}></GifsItem>
            ))}
        </section>
    )
}

export default GifsGrid